#app {
	/**
	 * 
	 * All headers and Paragraphs regardless of page/component 
	 *
	 * */

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-family: $header-font;
	}

	p {
		font-family: $body-font;
		font-size: 20px;
		line-height: 1.5;
	}
}

/**
 * 
 * 
 * ###### AUTHENTICATION PAGES ##########
 * 
 *	Login & Register page
 * */

@include respond-below(md) {
	#intro-background {
		.auth-intro {
			h1 {
				font-size: 30px !important;
			}
		}
	}
}

.orderbook-authentication {
	height: 100vh;

	#intro-background {
		background-size: cover;
		background-position: center;
		position: relative;

		.auth-intro {
			position: absolute;
			z-index: 2;

			.orderbook-icon {
				font-family: $header-font;
				font-weight: 900;
				height: 40px;
				width: 200px;
			}

			padding: 8.5em 2em;

			h1 {
				font-weight: 900;
				font-size: 40px;
				line-height: 1.3;
				color: $white;
				margin-bottom: 15px;
				max-width: 450px;
				text-shadow: 1px 1px 20px black;
			}

			p {
				font-size: 20px;
				max-width: 450px;
				color: $white;
				line-height: 1.3;
			}
		}

		#overlay {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: rgba(0, 38, 118, 0.6);
			z-index: 1;
		}
	}

	#orderbook-form {
		form {
			#orderbook-home {
				font-size: 35px;
				font-weight: bold;
				line-height: 1.3;
			}

			h2 {
				font-size: 30px;
				font-weight: bold;
				line-height: 1.2;
			}

			p {
				font-size: 18px;
			}

			.form-field {
				padding: 15px;
				border: none !important;

				&:focus {
					border: none !important;
				}

				&::-webkit-input-placeholder {
					/* Edge */
					color: $black;
				}

				&:-ms-input-placeholder {
					/* Internet Explorer 10-11 */
					color: $black;
				}

				&::placeholder {
					color: $black;
				}
			}

			.auth-button {
				width: 100% !important;
				background: $primary-dark;
				height: 50px;
				color: white;
				font-weight: bold;
				font-size: larger;

				&:disabled {
					cursor: not-allowed;
					background: lightgray;
				}
			}

			.account-signal {
				a {
					font-weight: bold;
					font-size: 17px;
				}
			}
		}
	}
}