#orderbook-investor-offers {
	position: relative;

	@include respond-below(sm) {
		padding: 0 !important;

		#offer {
			#header {
				margin-bottom: 2em !important;
				padding: 1em 0 0 1em !important;
			}
		}

		.accept-modal, .reject-modal {
			#modal-content {
				width: 80% !important;
			}
		}
	}

	@include respond-below(md) {
		#the-offer {
			.grid {
				#offer-description {
					padding: 20px !important;
				}

				#loan-summary {
					overflow-x: scroll !important;
				}
			}
		}
	}

	#offer {
		#header {
			font-size: 30px;
			color: $white;
			font-weight: bold;
			margin-bottom: 3em;
		}

		#the-offer {
			background: $white;

			.grid {
				#offer-header {
					background: $grey;
					padding: 0 35px;

					img {
					}

					h3 {
						text-decoration: underline;
						margin-top: 10px;
					}

					#schedule-payment {
						p {
							font-weight: 400;
							font-size: 15px;
							margin: 5px 0;
						}

						.grid {
							div {
								background: lightgray;
								text-align: center;
								padding: 8px;
								font-weight: bold;
							}
						}

						#address {
							margin-top: 20px;

							address {
								font-family: $body-font;
								font-style: normal;

								p {
									span {
										margin-right: 10px;
									}
								}
							}
						}
					}
				}

				#offer-description {
					// font-size: 17px;

					#description {
						font-size: 18px;
					}
				}

				#offer-pitch {
					background: $primary-dark;
					color: $white;

					p {
						font-size: 16px;
					}
				}
			}
		}
	}

	#offer-table {
		table {
			background: #e5e5e5;
			tbody {
				tr {
					td {
						text-align: center !important;
						padding: 10px;
						border-right: none !important;
					}
				}
			}
		}
	}

	#offer-signature {
		a {
			font-family: $header-font;
		}

		div {
			span {
				font-family: $signature-font;
				font-size: 25px;
				text-align: center;
			}
			h4 {
				font-size: 20px;
			}
		}
	}

	#offer-buttons {
		background: #e5e5e5;

		button {
			width: 200px;
		}
	}

	#offer-modal {
		display: none;

		&.accept-modal, &.reject-modal {
			display: block;
			position: fixed;
			z-index: 1;
			left: 0;
			top: 0;
			width: 100%; /* Full width */
			height: 100%; /* Full height */
			overflow: auto; /* Enable scroll if needed */
			background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */

			#modal-content {
				background: $white;
				padding: 20px;
				width: 40%;
				height: 40%;
				margin: 15% auto;

				.close {
					color: #aaa;
					font-size: 28px;
					font-weight: bold;
					display: block;
					text-align: right;

					&:hover,
					&:focus {
						color: black;
						text-decoration: none;
						cursor: pointer;
					}
				}

				button {
					width: 200px;
				}
			}
		}
	}
}