/*
* SMALL SCREENS
*/
@include respond-below(sm) {
	#orderbook-header {
		background-position: center !important;
		display: flex;
		justify-content: center;
		align-items: center;

		#orderbook-intro {
			display: flex;
			flex-direction: column !important;
			align-items: center !important;
			padding: 30px !important;

			h1,
			p {
				text-align: center;
			}

			h1 {
				font-size: 30px !important;
			}
		}
	}

	#orderbook-rows {
		flex-direction: column !important;
		align-items: center !important;

		.container {
			width: 100% !important;

			&.right-border {
				border-right: none !important;
			}

			h3 {
				margin-block-start: 0px !important;
			}
		}
	}

	#orderbook-bids {
		#bid-intro {
			display: flex;
			flex-direction: column;
			align-items: center;
			height: auto;

			h2,
			p {
				text-align: center;
			}

			h2 {
				font-size: 25px !important;
				text-align: center;
			}
		}
		#bid-banner {
			display: none !important;
		}
	}

	#orderbook-request {
		#request-intro-container {
			#request-banner {
				display: none !important;
			}

			#request-intro {
				display: flex;
				flex-direction: column;
				align-items: center;
				height: auto;

				h2,
				p {
					text-align: center;
				}

				h2 {
					font-size: 25px !important;
					text-align: center;
				}
			}
		}

		#orderbook-steps {
			flex-direction: column !important;
			height: auto !important;
			padding: 40px !important;

			.request-step {
				width: 100% !important;
			}
		}
	}
}

/*
* LARGE SCREENS
*/
#app {
	#orderbook-header {
		max-width: 100%;
		min-height: 75vh;
		background-repeat: no-repeat;
		background-position: top left;
		background-size: cover;

		#orderbook-intro {
			padding: 6em 0 0 5em;

			h1 {
				font-family: $header-font;
				font-size: 40px;
				font-weight: 900;
				max-width: 450px;
				margin-block-start: 0px;
			}

			p {
				font-family: $body-font;
				font-size: 20px;
				margin-bottom: 40px;
				max-width: 450px;
			}

			.intro-cta {
				background-color: $primary-dark;
			}
		}
	}

	#orderbook-rows {
		display: flex;
		padding: 30px;

		.container {
			// width: 33.3%;
			padding: 30px 0px 30px 30px;

			/*.gray-marker {
				background-color: lightgray;
				width: 45px;
				height: 45px;
				border-radius: 22.5px;
				margin-right: 10px;
				display: flex;
				justify-self: center;
				align-items: center;
			}*/

			.row-details {
				// width: 80%;
				// padding-left: 20px;

				h3 {
					display: inline-block;
					margin-bottom: 0px;
					margin-block-start: 0px;
					font-size: 23px;
					margin-bottom: 10px;

					.fa-thumbs-o-up {
						width: 45px;
						height: 45px;
						border-radius: 22.5px;
						background: lightgray;
						color: darkgray;
						text-align: center;
						vertical-align: middle;
						padding: 10px 0px;
						margin-right: 10px;
					}
				}
				p {
					font-size: 18px;
					padding-left: 20px;
				}
			}
		}
	}

	#orderbook-bids {
		// display: flex;

		.bid-container {
			&#bid-intro {
				background: $primary-dark;
				padding: 3.5em 4em;
				// width: 50%;

				h2 {
					color: $white;
					font-size: 40px;
					font-weight: 900;
					margin-bottom: 15px;
				}

				p {
					max-width: 500px;
					color: $white;
					margin-bottom: 40px;
				}

				.bid-cta {
					color: $white;
					background: inherit;
					border: 1.5px solid $white;
				}
			}

			&#bid-banner {
				width: 100%;
				display: block;
			}
		}
	}

	#orderbook-request {
		#request-intro-container {
			// display: flex;

			#request-banner {
				width: 100%;
				display: block;
			}

			#request-intro {
				padding: 3.5em 4em;

				h2 {
					font-size: 40px;
					padding-right: 40px;
					margin-bottom: 15px;
				}

				p {
					max-width: 500px;
					margin-bottom: 40px;
				}

				.request-cta {
					background: $primary-dark;
					a {
						font-size: 15.5px;
					}
				}
			}
		}

		#orderbook-steps {
			background: #0f0f0f;
			height: 400px;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 0px 40px;

			.request-step {
				width: 25%;
				padding: 10px;

				.white-marker {
					width: 45px;
					height: 45px;
					border-radius: 22.5px;
					background: $white;
					text-align: center;
					font-family: roman;
					font-size: 30px;
					font-weight: bolder;
					display: flex;
					align-items: center;
					justify-content: center;
					margin-bottom: 35px;
				}

				h4,
				p {
					color: $white;
				}

				h4 {
					font-size: 20px;
					margin-bottom: 10px;
				}

				p {
					font-size: 14.5px;
				}
			}
		}
	}
}